import styled from 'styled-components';

const MediaFieldWrapper = styled.div`
  display: flex;
  i {
    font-size: 20px;
  }
  .content {
    padding-left: 10px;
    color: #4a4a4a;
    font-size: 12px;
  }
`;
export default MediaFieldWrapper;



